import React, { useState, useEffect } from 'react';
import { 
  Box, 
  Typography, 
  Card, 
  CardContent, 
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Divider,
  Switch,
  Container,
  Slider
} from '@mui/material';
import { 
  Settings,
  Notifications,
  Brightness4,
  Help,
  Info,
  Refresh,
  FilterList
} from '@mui/icons-material';
import { useTheme } from '../contexts/ThemeContext';
import { useWordProgress } from '../hooks/usePersistentState';

// Props interface fr InstallningarPage
interface InstallningarPageProps {
  onShowHelp: () => void;
}

// Instllningar-sidan - hr kommer anvndare att kunna anpassa appen
const InstallningarPage: React.FC<InstallningarPageProps> = ({ onShowHelp }) => {
  // Hmta tema-funktionalitet frn Theme Context
  const { mode, toggleTheme } = useTheme();
  // Hmta wordProgress fr att kunna nollstlla
  const { setWordProgress } = useWordProgress();
  
  // State fr antal lrda ord att repetera
  const [reviewLearnedWords, setReviewLearnedWords] = useState<number>(2);
  
  // State fr att bara visa meningar med meningsniv
  const [sentencesOnlyWithLevel, setSentencesOnlyWithLevel] = useState<boolean>(true);
  
  // Ladda instllningar frn localStorage vid komponentens mount
  useEffect(() => {
    const savedReviewWords = localStorage.getItem('reviewLearnedWords');
    if (savedReviewWords !== null) {
      setReviewLearnedWords(parseInt(savedReviewWords));
    }
    
    const savedSentencesLevel = localStorage.getItem('sentences-only-with-level');
    if (savedSentencesLevel !== null) {
      setSentencesOnlyWithLevel(savedSentencesLevel === 'true');
    }
  }, []);
  
  // Spara instllning till localStorage nr den ndras
  const handleReviewLearnedWordsChange = (event: Event, newValue: number | number[]) => {
    const value = Array.isArray(newValue) ? newValue[0] : newValue;
    setReviewLearnedWords(value);
    localStorage.setItem('reviewLearnedWords', value.toString());
  };

  // Hantera ndring av meningar-instllning
  const handleSentencesOnlyWithLevelChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = event.target.checked;
    setSentencesOnlyWithLevel(newValue);
    localStorage.setItem('sentences-only-with-level', newValue.toString());
  };

  // Funktion fr att nollstlla alla instllningar och progress
  const handleReset = () => {
    const confirmed = window.confirm(
      'r du sker p att du vill nollstlla alla instllningar och progress?\n\n' +
      'Detta kommer att:\n' +
      ' Rensa alla ordlistor och progress\n' +
      ' terstlla alla instllningar\n' +
      ' Ta bort all sparad data\n\n' +
      'Denna tgrd kan inte ngras!'
    );
    
    if (confirmed) {
      // Rensa localStorage helt
      localStorage.clear();
      
      // Ta bort specifika nycklar fr att vara sker
      localStorage.removeItem('wordProgress');
      localStorage.removeItem('hasSeenStartGuide');
      localStorage.removeItem('theme');
      localStorage.removeItem('spelling-playback-speed');
      localStorage.removeItem('spelling-interval');
      localStorage.removeItem('reviewLearnedWords');
      localStorage.removeItem('sentences-only-with-level');
      
      // Nollstll wordProgress
      setWordProgress({});
      
      // Visa bekrftelse
      alert('Alla instllningar och progress har nollstllts! Appen kommer att starta om frn brjan.');
      
      // Ladda om sidan fr att skerstlla att allt r nollstllt
      window.location.reload();
    }
  };

  return (
    // Container som centrerar innehllet och ger padding
    <Container maxWidth="sm" sx={{ py: 3 }}>

      {/* Lista med instllningsalternativ */}
      <Card>
        <List>
          {/* Notifikationer */}
          <ListItem>
            <ListItemIcon>
              <Notifications />
            </ListItemIcon>
            <ListItemText 
              primary="Notifikationer" 
              secondary="F pminnelser om att va"
            />
            <Switch edge="end" />
          </ListItem>
          
          <Divider />
          
          {/* Mrkt tema */}
          <ListItem>
            <ListItemIcon>
              <Brightness4 />
            </ListItemIcon>
            <ListItemText 
              primary="Mrkt tema" 
              secondary={mode === 'dark' ? 'Mrkt tema aktiverat' : 'Anvnd mrk bakgrund'}
            />
            <Switch 
              edge="end" 
              checked={mode === 'dark'}
              onChange={toggleTheme}
            />
          </ListItem>
          
          <Divider />
          
          {/* Meningar med meningsniv */}
          <ListItem>
            <ListItemIcon>
              <FilterList />
            </ListItemIcon>
            <ListItemText 
              primary="Meningar med meningsniv" 
              secondary={sentencesOnlyWithLevel ? 'Visa bara meningar med svrighetsniv' : 'Visa alla meningar'}
            />
            <Switch 
              edge="end" 
              checked={sentencesOnlyWithLevel}
              onChange={handleSentencesOnlyWithLevelChange}
            />
          </ListItem>
          
          <Divider />
          
          {/* Repetition av lrda ord */}
          <ListItem>
            <ListItemIcon>
              <Refresh />
            </ListItemIcon>
            <ListItemText 
              primary="Repetition av lrda ord" 
              secondary={`${reviewLearnedWords} ord repeteras vid varje vning`}
            />
          </ListItem>
          
          {/* Slider fr antal lrda ord att repetera */}
          <ListItem>
            <Box sx={{ width: '100%', px: 2, pb: 2 }}>
              <Typography variant="body2" color="text.secondary" gutterBottom>
                Antal lrda ord som ska repeteras (0-5)
              </Typography>
              <Slider
                value={reviewLearnedWords}
                onChange={handleReviewLearnedWordsChange}
                min={0}
                max={5}
                step={1}
                marks={[
                  { value: 0, label: '0' },
                  { value: 1, label: '1' },
                  { value: 2, label: '2' },
                  { value: 3, label: '3' },
                  { value: 4, label: '4' },
                  { value: 5, label: '5' }
                ]}
                valueLabelDisplay="auto"
                sx={{ mt: 1 }}
              />
              <Typography variant="caption" color="text.secondary">
                {reviewLearnedWords === 0 
                  ? 'Inga lrda ord repeteras - fokus p nya ord' 
                  : `${reviewLearnedWords} lrda ord repeteras fr att frhindra glmska`
                }
              </Typography>
            </Box>
          </ListItem>
        </List>
      </Card>

      {/* Ytterligare instllningskategorier */}
      <Box sx={{ mt: 3 }}>
        <Card>
          <List>
            {/* Hjlp */}
            <ListItem button onClick={onShowHelp}>
              <ListItemIcon>
                <Help />
              </ListItemIcon>
              <ListItemText 
                primary="Hjlp" 
                secondary="F hjlp med att anvnda appen"
              />
            </ListItem>
            
            <Divider />
            
            {/* Nollstll */}
            <ListItem button onClick={handleReset}>
              <ListItemIcon>
                <Refresh />
              </ListItemIcon>
              <ListItemText 
                primary="Nollstll allt" 
                secondary="Rensa alla instllningar och progress"
              />
            </ListItem>
            
            <Divider />
            
            {/* Om appen */}
            <ListItem button>
              <ListItemIcon>
                <Info />
              </ListItemIcon>
              <ListItemText 
                primary="Om TSP Skolan" 
                secondary="Version 1.0.0"
              />
            </ListItem>
          </List>
        </Card>
      </Box>

      {/* Platshllare fr framtida instllningar */}
      <Box sx={{ mt: 3 }}>
        <Card>
          <CardContent>
            <Typography variant="h6" gutterBottom>
              Kommande funktioner
            </Typography>
            <Typography variant="body2" color="text.secondary">
              Fler instllningsalternativ kommer att lggas till i framtida versioner.
            </Typography>
          </CardContent>
        </Card>
      </Box>
    </Container>
  );
};

export default InstallningarPage;
