import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { 
  WordDatabase, 
  PhraseDatabase, 
  loadWordDatabase, 
  loadPhraseDatabase 
} from '../types/database';

// Interface fr context-vrdet
interface DatabaseContextType {
  wordDatabase: WordDatabase;
  phraseDatabase: PhraseDatabase;
  isLoading: boolean;
  error: string | null;
  refreshDatabases: () => Promise<void>;
}

// Skapar context med default-vrden
const DatabaseContext = createContext<DatabaseContextType>({
  wordDatabase: {},
  phraseDatabase: {},
  isLoading: false,
  error: null,
  refreshDatabases: async () => {},
});

// Hook fr att anvnda databasen context
export const useDatabase = () => {
  const context = useContext(DatabaseContext);
  if (!context) {
    throw new Error('useDatabase mste anvndas inom en DatabaseProvider');
  }
  return context;
};

// Props fr DatabaseProvider
interface DatabaseProviderProps {
  children: ReactNode;
}

// Provider-komponent som laddar och tillhandahller databaserna
export const DatabaseProvider: React.FC<DatabaseProviderProps> = ({ children }) => {
  // State fr att hlla databaserna
  const [wordDatabase, setWordDatabase] = useState<WordDatabase>({});
  const [phraseDatabase, setPhraseDatabase] = useState<PhraseDatabase>({});
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // Funktion fr att ladda databaserna
  const loadDatabases = async () => {
    try {
      setIsLoading(true);
      setError(null);
      
      // Laddar bda databaserna parallellt fr bttre prestanda
      const [words, phrases] = await Promise.all([
        loadWordDatabase(),
        loadPhraseDatabase()
      ]);
      
      setWordDatabase(words);
      setPhraseDatabase(phrases);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Ett fel uppstod vid laddning av databaserna');
      console.error('Fel vid laddning av databaserna:', err);
    } finally {
      setIsLoading(false);
    }
  };

  // Funktion fr att uppdatera databaserna
  const refreshDatabases = async () => {
    await loadDatabases();
  };

  // Laddar databaserna nr komponenten mountas
  useEffect(() => {
    loadDatabases();
  }, []);

  // Context-vrde som skickas till alla barn-komponenter
  const value: DatabaseContextType = {
    wordDatabase,
    phraseDatabase,
    isLoading,
    error,
    refreshDatabases,
  };

  return (
    <DatabaseContext.Provider value={value}>
      {children}
    </DatabaseContext.Provider>
  );
};
